import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import { Box, Grid, Hidden, Zoom } from "@material-ui/core";
import "./portfolio.scss";
import { IReduxState } from "../../store/slices/state.interface";
import { IPortFolioSlice, loadPortFolioDetails, handleClaimBond, handleClaimBoom } from "../../store/slices/portfolio-slice";
import { useWeb3Context } from "../../hooks";
import Loading from "../../components/Loader";
import InfoCardList from "../../components/InfoCardList";
import { loadDashboardDetails } from "../../store/slices/dashboard-slice";
import { loadBondDetails } from "src/store/slices/bond-slice";
import { loadTaxAndZoneDetails } from "src/store/slices/tax-zone-slice";
import { loadTreasuryDetails } from "src/store/slices/treasury-slice";

function Portfolio() {
  const isAppLoading = useSelector<IReduxState, boolean>(state => state.portFolio.loading);
  const app = useSelector<IReduxState, IPortFolioSlice>(state => state.portFolio);
  const dispatch = useDispatch();
  const { connect, provider, hasCachedProvider, chainID, connected, address } = useWeb3Context();

  useEffect(() => {
    dispatch(loadPortFolioDetails({ networkID: chainID, provider, userAddress: address }));
  }, []);

  useEffect(() => {
    const interval = setInterval(() => {
      if (!address || isAppLoading) return;
      dispatch(loadPortFolioDetails({ networkID: chainID, provider, userAddress: address }));
      dispatch(loadDashboardDetails({ networkID: chainID, provider }));
      dispatch(loadBondDetails({ networkID: chainID, provider, userAddress: address }));
      dispatch(loadTaxAndZoneDetails({ networkID: chainID, provider, userAddress: address }));
      dispatch(loadTreasuryDetails({ networkID: chainID, provider, userAddress: address }));
    }, 10000);
    return () => clearInterval(interval);
  }, [address]);

  const claimClicked = () => {
    console.log("ddddddddddddddddddddd");
    dispatch(handleClaimBoom({ provider, networkID: chainID }));
  };

  const claimBondClicked = () => {
    dispatch(handleClaimBond({ provider, networkID: chainID }));
  };

  const firstElementList = [
    {
      name: "Held In Wallet",
      value: "457,154,124 BOOM",
    },
    {
      name: "Vesting in Bonds",
      value: "457,154,124 BOOM",
    },
    {
      name: "Leverage Position",
      value: "457,154,124 BOOM",
    },
    {
      name: "Owned in TReasury",
      value: "457,154,124 BOOM",
    },
  ];

  const secondElementList = [
    {
      name: "Average Purchase Price",
      value: "0.001245 USDC",
    },
    {
      name: "Time Remaining to Vest",
      value: "0 seconds",
    },
    {
      name: "Last Bonding Time",
      value: "2022/05/28 13:05",
    },
    {
      name: "Bonded Amount Claimable",
      value: "8,457,781",
      isButton: true,
      buttonLabel: "Claim",
      onClick: claimClicked,
    },
  ];

  const thirdElementList = [
    {
      name: "Total Rewards",
      value: "0 WETH",
    },
    {
      name: "Total Claimed to Date",
      value: "0 WETH",
    },
    {
      name: "Last Claim Time",
      value: "0",
    },
    {
      name: "Total Claimable Rewards",
      value: "0",
      isButton: true,
      buttonLabel: "Claim",
      onClick: claimBondClicked,
    },
  ];

  const firstHead = {
    title: "Total Holding",
    value: "494,451,475",
    unit: "BOOM",
  };

  const secondHead = {
    title: "Wallet Balance",
    value: "494,451,475",
    unit: "BOOM",
  };

  const thirdHead = {
    title: "Total Holding",
    value: "494,451,475",
    unit: "BOOM",
  };

  if (isAppLoading) return <Loading />;
  return (
    <div className="portfolio-view">
      <div className="portfolio-infos-wrap">
        <div className="portfolio-head">Portfolio</div>
        <Hidden smDown>
          <div className="portfolio-top">
            <Grid container spacing={2}>
              <Grid item lg={4} md={4} sm={12} xs={12}>
                <div className="portfolio-top-content child-boder">
                  <div className="portfolio-top-content-title">Total Holding</div>
                  <Box display="flex" alignItems="baseline" style={{ gap: "6px" }}>
                    <div className="portfolio-top-content-value">494,451,475</div>
                    <div className="portfolio-top-content-unit">BOOM</div>
                  </Box>
                </div>
              </Grid>
              <Grid item lg={4} md={4} sm={12} xs={12}>
                <div className="portfolio-top-content child-boder">
                  <div className="portfolio-top-content-title">Total Bonding</div>
                  <Box display="flex" alignItems="baseline" style={{ gap: "6px" }}>
                    <div className="portfolio-top-content-value">800,000,000</div>
                    <div className="portfolio-top-content-unit">BOOM</div>
                  </Box>
                </div>
              </Grid>
              <Grid item lg={4} md={4} sm={12} xs={12}>
                <div className="portfolio-top-content">
                  <div className="portfolio-top-content-title">Boom Balance</div>
                  <Box display="flex" alignItems="baseline" style={{ gap: "6px" }}>
                    <div className="portfolio-top-content-value">1,000,000,000</div>
                    <div className="portfolio-top-content-unit">BOOM</div>
                  </Box>
                </div>
              </Grid>
            </Grid>
          </div>
        </Hidden>
        <div className="portfolio-container">
          <Zoom in={true}>
            <Grid container spacing={2}>
              <Grid item lg={4} md={4} sm={12} xs={12}>
                <InfoCardList dataList={firstElementList} height={184} headerInfo={firstHead} />
              </Grid>
              <Grid item lg={4} md={4} sm={12} xs={12}>
                <InfoCardList dataList={secondElementList} height="184" headerInfo={secondHead} />
              </Grid>
              <Grid item lg={4} md={4} sm={12} xs={12}>
                <InfoCardList dataList={thirdElementList} height="184" headerInfo={thirdHead} />
              </Grid>
            </Grid>
          </Zoom>
        </div>
      </div>
    </div>
  );
}

export default Portfolio;
