import { ethers } from "ethers";
import { getAddresses } from "../constants";
import { MULTICALLABI } from "../abi";

export type MultiCallResponse<T> = T | null;

const getMulticallContract = (address: string, signer?: ethers.Signer | ethers.providers.Provider) => {
  return new ethers.Contract(address, MULTICALLABI, signer);
};

export interface Call {
  address: string; // Address of the contract
  name: string; // Function name on the contract (example: balanceOf)
  params?: any[]; // Function params
}

export interface CallWithAbi {
  address: string; // Address of the contract
  name: string; // Function name on the contract (example: balanceOf)
  params?: any[]; // Function params
  abi?: any;
}
interface MulticallOptions {
  requireSuccess?: boolean;
}

export const multicall = async (abi: any[], calls: Call[], networkProvider: any, chainId: any) => {
  try {
    const multi = getMulticallContract(getAddresses(chainId).MULITI_CALL, networkProvider);
    const itf = new ethers.utils.Interface(abi);

    const calldata = calls.map(call => [call.address.toLowerCase(), itf.encodeFunctionData(call.name, call.params)]);
    const { returnData } = await multi.aggregate(calldata);
    const res = returnData.map((call: any, i: number) => itf.decodeFunctionResult(calls[i].name, call));

    return res;
  } catch (error) {
    console.log("error = ", error);
    return [];
  }
};

/**
 * Multicall V2 uses the new "tryAggregate" function. It is different in 2 ways
 *
 * 1. If "requireSuccess" is false multicall will not bail out if one of the calls fails
 * 2. The return inclues a boolean whether the call was successful e.g. [wasSuccessfull, callResult]
 */
export const multicallv2 = async <T = any>(
  abi: any[],
  calls: Call[],
  options: MulticallOptions = { requireSuccess: true },
  networkProvider: any,
  chainId = 56,
): Promise<MultiCallResponse<T>> => {
  const { requireSuccess } = options;
  const multi = getMulticallContract(getAddresses(chainId).MULITI_CALL, networkProvider);
  const itf = new ethers.utils.Interface(abi);

  const calldata = calls.map(call => [call.address.toLowerCase(), itf.encodeFunctionData(call.name, call.params)]);
  const returnData = await multi.tryAggregate(requireSuccess, calldata);
  const res = returnData.map((call: any, i: number) => {
    const [result, data] = call;
    return result ? itf.decodeFunctionResult(calls[i].name, data) : null;
  });

  return res;
};

export default multicall;
