import { useDispatch, useSelector } from "react-redux";
import React, { useEffect } from "react";
import { Box, Grid, Zoom } from "@material-ui/core";
import { trim } from "../../helpers";
import "./bond.scss";
import { Skeleton } from "@material-ui/lab";
import { IReduxState } from "../../store/slices/state.interface";
import { IDashboardSlice } from "../../store/slices/dashboard-slice";
import { IBond, IBondSlice } from "../../store/slices/bond-slice";
import { useWeb3Context } from "../../hooks";
import Loading from "../../components/Loader";
import { loadDashboardDetails } from "../../store/slices/dashboard-slice";
import { loadBondDetails } from "../../store/slices/bond-slice";
import InfoCard from "../../components/InfoCard";
import InfoCardList from "../../components/InfoCardList";
import BondModal from "../../components/Modal/BondModal";
import { loadPortFolioDetails } from "src/store/slices/portfolio-slice";
import { loadTaxAndZoneDetails } from "src/store/slices/tax-zone-slice";
import { loadTreasuryDetails } from "src/store/slices/treasury-slice";

function Bond() {
  const isAppLoading = useSelector<IReduxState, boolean>(state => state.bonding.loading);
  const bond = useSelector<IReduxState, IBondSlice>(state => state.bonding);
  const dispatch = useDispatch();
  const { connect, provider, hasCachedProvider, chainID, connected, address } = useWeb3Context();
  const [open, setOpen] = React.useState(false);
  const [bondData, setBondData] = React.useState({});
  const handleClose = () => setOpen(false);

  console.log("-----bond---------", bond);

  const onBond = (_bond: any) => {
    console.log("_bond", _bond);
    setBondData(_bond);
    setOpen(true);
  };

  useEffect(() => {
    dispatch(loadBondDetails({ networkID: chainID, provider, userAddress: address }));
  }, []);

  useEffect(() => {
    const interval = setInterval(() => {
      if (!address || isAppLoading) return;
      dispatch(loadBondDetails({ networkID: chainID, provider, userAddress: address }));
      dispatch(loadDashboardDetails({ networkID: chainID, provider }));
      dispatch(loadPortFolioDetails({ networkID: chainID, provider, userAddress: address }));
      dispatch(loadTaxAndZoneDetails({ networkID: chainID, provider, userAddress: address }));
      dispatch(loadTreasuryDetails({ networkID: chainID, provider, userAddress: address }));
    }, 20000);
    return () => clearInterval(interval);
  }, [address]);

  if (isAppLoading) return <Loading />;
  return (
    <div className="dashboard-view">
      <BondModal open={open} bondData={bondData} handleClose={handleClose} />
      <div className="dashboard-infos-wrap">
        <div className="dashboard-head">BONDING</div>
        <div className="dashboard-container">
          <Zoom in={true}>
            <Grid container spacing={4}>
              <Grid item lg={4} md={6} sm={12} xs={12}>
                <InfoCard title="Amount claimed to date" value="0" unit="BOOM" />
              </Grid>
              <Grid item lg={4} md={6} sm={12} xs={12}>
                <InfoCard title="Amount Vesting" value="8,874,124" unit="BOOM" />
              </Grid>
              <Grid item lg={4} md={6} sm={12} xs={12}>
                <InfoCard title="Active Bonds Available" value="8,874,124" unit="BOOM" />
              </Grid>
              <Grid item lg={4} md={6} sm={12} xs={12}>
                <InfoCard title="Total amount available to bond" value="8,874,124" unit="BOOM" />
              </Grid>
              <Grid item lg={4} md={6} sm={12} xs={12}>
                <InfoCard title="Current Vesting Term" value="5" unit="minutes" />
              </Grid>
              <Grid item lg={4} md={6} sm={12} xs={12}>
                <InfoCard title="Current Bonding ROI" value="256" unit="%" />
              </Grid>
            </Grid>
          </Zoom>
        </div>
        <div className="bond-data" style={{ marginTop: "80px", marginBottom: "80px", color: "white" }}>
          Description: Purchasing or claiming the bond is not taxed. Purchased bonds claim rewards at full face value from the moment of purchase. you need to hold tokens in wallet
          (purchase them first in the market) to bond - maximum bonding amount = 5x of amount held in wallet
        </div>
        <div className="dashboard-head" style={{ marginBottom: "40px" }}>
          BONDING TERMS
        </div>
        <div className="inline-block min-w-full shadow rounded-lg overflow-hidden" style={{ border: "2px solid #29C5F6", borderRadius: "8px" }}>
          <table className="min-w-full leading-normal" style={{ width: "100%", borderCollapse: "collapse" }}>
            <thead className="bg-gradient-to-r from-gradient_from to-gradient_to hover:from-gradient_to hover:to-gradient_from text-gray-100" style={{ background: "#29C5F6" }}>
              <tr>
                <th className="px-5 py-3 border-b-2 text-left text-xs font-semibold uppercase tracking-wider">#</th>
                <th className="px-5 py-3 border-b-2 text-center text-xs font-semibold uppercase tracking-wider">Asset Bonded</th>
                <th className="px-5 py-3 border-b-2 text-center text-xs font-semibold uppercase tracking-wider">Token Price</th>
                <th className="px-5 py-3 border-b-2 text-center text-xs font-semibold uppercase tracking-wider">Vesting Terms</th>
                <th className="px-5 py-3 border-b-2 text-center text-xs font-semibold uppercase tracking-wider">Total Amount</th>
                <th className="px-5 py-3 border-b-2 text-center text-xs font-semibold uppercase tracking-wider">Amount Left</th>
                <th className="px-5 py-3 border-b-2 text-center text-xs font-semibold uppercase tracking-wider">Current ROI</th>
                <th className="px-5 py-3 border-b-2 text-center text-xs font-semibold uppercase tracking-wider"></th>
              </tr>
            </thead>
            <tbody>
              {bond?.bonds?.map((bond, index) => {
                return (
                  <tr key={index}>
                    <td className="px-5 py-5 border-b border-[#35cff270] bg-[#35cff221] text-sm">
                      <p className="whitespace-no-wrap" style={{ color: "white" }}>
                        {index + 1}
                      </p>
                    </td>
                    {/* <td className="px-5 py-5 border-b border-[#35cff270] text-sm">
                      <p className="whitespace-no-wrap text-center" style={{ color: "white" }}>
                        {bond?.bondingprice}
                      </p>
                    </td> */}
                    <td className="px-5 py-5 border-b border-[#35cff270] text-sm">
                      <p className="whitespace-no-wrap text-center" style={{ color: "white" }}>
                        {bond.assetBond}
                      </p>
                    </td>
                    <td className="px-5 py-5 border-b border-[#35cff270] text-sm">
                      <p className="whitespace-no-wrap text-center" style={{ color: "white" }}>
                        {bond?.tokenPrice}
                      </p>
                    </td>
                    <td className="px-5 py-5 border-b border-[#35cff270] text-sm">
                      <p className="whitespace-no-wrap text-center" style={{ color: "white" }}>
                        5mins
                      </p>
                    </td>
                    <td className="px-5 py-5 border-b border-[#35cff270] text-sm">
                      <p className="whitespace-no-wrap text-center" style={{ color: "white" }}>
                        {bond?.totalAmount}
                      </p>
                    </td>
                    <td className="px-5 py-5 border-b border-[#35cff270] text-sm">
                      <p className="whitespace-no-wrap text-center" style={{ color: "white" }}>
                        {bond?.amountLeft}
                      </p>
                    </td>
                    <td className="px-5 py-5 border-b border-[#35cff270] text-sm">
                      <p className="whitespace-no-wrap text-center" style={{ color: "white" }}>
                        256.60%
                      </p>
                    </td>
                    <td className="px-5 py-5 border-b border-[#35cff270] text-sm">
                      <button className="bond-one-table-btn" style={{ borderRadius: "10px", background: "#29C5F6" }} onClick={() => onBond(bond)}>
                        Bond
                      </button>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}

export default Bond;
