import { ethers } from "ethers";
import { getAddresses, Networks } from "../../constants";
import { BondDepositoryABI, BoomTaxManagerABI, BoomPriceFetcherABI, BoomerangCapitalABI, BoomTreasuryABI, BoomMetricsTrackerABI, BoomSafeLeverageABI } from "../../abi";
import { setAll } from "../../helpers";
import { createSlice, createSelector, createAsyncThunk } from "@reduxjs/toolkit";
import { JsonRpcProvider } from "@ethersproject/providers";
import { RootState } from "../store";
import { multicall } from "../../helpers/multicall";

interface ILoadDashboardDetails {
  networkID: number;
  provider: JsonRpcProvider;
}

export const loadDashboardDetails = createAsyncThunk(
  "app/loadDashboardDetails",
  //@ts-ignore
  async ({ networkID, provider }: ILoadDashboardDetails) => {
    const addresses = getAddresses(networkID);
    const currentBlock = await provider.getBlockNumber();
    const currentBlockTime = (await provider.getBlock(currentBlock)).timestamp;
    const boomDepositoryContract = new ethers.Contract(addresses.BOND_DEPOSITORY, BondDepositoryABI, provider);
    const priceFetcherContract = new ethers.Contract(addresses.PRICE_FETCHER_ADDRESS, BoomPriceFetcherABI, provider);
    const treasuryContract = new ethers.Contract(addresses.TREASURY_CAP_ADDRESS, BoomTreasuryABI, provider);
    const leverageContract = new ethers.Contract(addresses.LEVERAGE_ADDRESS, BoomSafeLeverageABI, provider);

    const priceInUSD = await priceFetcherContract.getTokenPriceInUSDC18Dec();
    const wETHPriceInUSDC18Dec = await priceFetcherContract.getWETHPriceInUSDC18Dec();
    const taxManagerCalls = [
      {
        address: addresses.TAXMANAGER_ADDRESS,
        name: "getCurrentTaxRegimeArray",
        params: [],
      },
      {
        address: addresses.TAXMANAGER_ADDRESS,
        name: "getCurrentFairPrice",
        params: [],
      },
      {
        address: addresses.TAXMANAGER_ADDRESS,
        name: "getCurrentBackingPrice",
        params: [],
      },
      {
        address: addresses.TAXMANAGER_ADDRESS,
        name: "getCirculatingSupply",
        params: [],
      },
      {
        address: addresses.TAXMANAGER_ADDRESS,
        name: "getAmountInHoldersWallets",
        params: [],
      },
      {
        address: addresses.TAXMANAGER_ADDRESS,
        name: "getAmountOfTokensInLP",
        params: [],
      },
      {
        address: addresses.TAXMANAGER_ADDRESS,
        name: "getAmountOfTokensCompoundingInDividendTracker",
        params: [],
      },
      {
        address: addresses.TAXMANAGER_ADDRESS,
        name: "getAmountOfTokensInTreasury",
        params: [],
      },
      {
        address: addresses.TAXMANAGER_ADDRESS,
        name: "getAmountOfTokensForBonding",
        params: [],
      },
      {
        address: addresses.TAXMANAGER_ADDRESS,
        name: "getAmountOfTokensInBondingDepo",
        params: [],
      },
    ];
    const [
      aaaaa,
      currentFairPrice,
      currentBackingPrice,
      circulatingSupply,
      amountInHoldersWallets,
      amountOfTokensInLP,
      amountOfTokensCDT,
      amountOfTokensInTreasury,
      amountOfTokensForBonding,
      amountOfTokensInBondingDepo,
    ] = await multicall(BoomTaxManagerABI, taxManagerCalls, provider, networkID);
    const boomerangCalls = [
      {
        address: addresses.BOOMCAPITAL_ADDRESS,
        name: "totalSupply",
        params: [],
      },
      {
        address: addresses.BOOMCAPITAL_ADDRESS,
        name: "balanceOf",
        params: [addresses.TREASURY_ADDRESS],
      },
      {
        address: addresses.BOOMCAPITAL_ADDRESS,
        name: "balanceOf",
        params: [addresses.TREASURY_WALLET_ADDRESS],
      },
      {
        address: addresses.BOOMCAPITAL_ADDRESS,
        name: "circulatingSupply",
        params: [],
      },
      {
        address: addresses.BOOMCAPITAL_ADDRESS,
        name: "balanceOf",
        params: [addresses.LEVERAGE_WALLET_ADDRESS],
      },
      {
        address: addresses.BOOMCAPITAL_ADDRESS,
        name: "balanceOf",
        params: [addresses.UNISWAP_PAIR_ADDRESS],
      },
    ];

    const [totalSupply, amountInTreasury, amountInBondingDepo, boomCirculatingSupply, balanceOfLeverageWallet, balanceOfUniswapPair] = await multicall(
      BoomerangCapitalABI,
      boomerangCalls,
      provider,
      networkID,
    );
    const currentTreasuryValue = await treasuryContract.currentTreasuryValue();
    const totalBondsVestingNow = await boomDepositoryContract.getTotalBondsVestingNow();

    const metricsTrackerCalls = [
      {
        address: addresses.METRIC_TRACKER_ADDRESS,
        name: "getTokenHoldersTotal",
        params: [],
      },
      {
        address: addresses.METRIC_TRACKER_ADDRESS,
        name: "getTokenHoldersWallet",
        params: [],
      },
      {
        address: addresses.METRIC_TRACKER_ADDRESS,
        name: "getTokenHoldersBondsVesting",
        params: [],
      },
      {
        address: addresses.METRIC_TRACKER_ADDRESS,
        name: "getTokenHoldersLeveragePosition",
        params: [],
      },
    ];

    const [totalHolders, holdersWalletToken, holdersBondsVesting, holdersLeverage] = await multicall(BoomMetricsTrackerABI, metricsTrackerCalls, provider, networkID);
    const tokensInLeverage = await leverageContract.getTotalTokensInLeverageContract();
    return {
      priceInUSD: ethers.utils.formatEther(priceInUSD),
      wETHPriceInUSDC18Dec: ethers.utils.formatEther(wETHPriceInUSDC18Dec),
      taxInfo: {
        currentFairPrice: ethers.utils.formatEther(currentFairPrice[0].toString()),
        currentBackingPrice: ethers.utils.formatEther(currentBackingPrice[0].toString()),
        circulatingSupply: ethers.utils.formatEther(circulatingSupply[0].toString()),
        amountInHoldersWallets: ethers.utils.formatEther(amountInHoldersWallets[0].toString()),
        amountOfTokensInLP: ethers.utils.formatEther(amountOfTokensInLP[0].toString()),
        amountOfTokensCDT: ethers.utils.formatEther(amountOfTokensCDT[0].toString()),
        amountOfTokensInTreasury: ethers.utils.formatEther(amountOfTokensInTreasury[0].toString()),
        amountOfTokensForBonding: ethers.utils.formatEther(amountOfTokensForBonding[0].toString()),
        amountOfTokensInBondingDepo: ethers.utils.formatEther(amountOfTokensInBondingDepo[0].toString()),
      },
      boomerang: {
        totalSupply: ethers.utils.formatEther(totalSupply[0].toString()),
        amountInTreasury: ethers.utils.formatEther(amountInTreasury[0].toString()),
        amountInBondingDepo: ethers.utils.formatEther(amountInBondingDepo[0].toString()),
        boomCirculatingSupply: ethers.utils.formatEther(boomCirculatingSupply[0].toString()),
        balanceOfLeverageWallet: ethers.utils.formatEther(balanceOfLeverageWallet[0].toString()),
        balanceOfUniswapPair: ethers.utils.formatEther(balanceOfUniswapPair[0].toString()),
      },
      metricTracker: {
        totalHolders: totalHolders[0].toString(),
        holdersWalletToken: holdersWalletToken[0].toString(),
        holdersBondsVesting: holdersBondsVesting[0].toString(),
        holdersLeverage: holdersLeverage[0].toString(),
      },
      tokensInLeverage: ethers.utils.formatEther(tokensInLeverage.toString()),
      totalBondsVestingNow: ethers.utils.formatEther(totalBondsVestingNow.toString()),
      currentTreasuryValue: ethers.utils.formatEther(currentTreasuryValue.toString()),
    };
  },
);

const initialState = {
  loading: true,
};

export interface IBoomeranInfo {
  totalSupply: any;
  amountInTreasury: any;
  amountInBondingDepo: any;
  boomCirculatingSupply: any;
  balanceOfLeverageWallet: any;
  balanceOfUniswapPair: any;
}
export interface ITaxInfo {
  currentFairPrice: any;
  currentBackingPrice: any;
  circulatingSupply: any;
  amountInHoldersWallets: any;
  amountOfTokensInLP: any;
  amountOfTokensCDT: any;
  amountOfTokensInTreasury: any;
  amountOfTokensForBonding: any;
  amountOfTokensInBondingDepo: any;
}
export interface IMetric {
  totalHolders: any;
  holdersWalletToken: any;
  holdersBondsVesting: any;
  holdersLeverage: any;
}

export interface IDashboardSlice {
  loading: boolean;
  priceInUSD: any;
  wETHPriceInUSDC18Dec: any;
  taxInfo: ITaxInfo;
  boomerang: IBoomeranInfo;
  metricTracker: IMetric;
  tokensInLeverage: any;
  totalBondsVestingNow: any;
  currentTreasuryValue: any;
}

const appSlice = createSlice({
  name: "dashboard",
  initialState,
  reducers: {
    fetchAppSuccess(state, action) {
      setAll(state, action.payload);
    },
  },
  extraReducers: builder => {
    builder
      .addCase(loadDashboardDetails.pending, (state, action) => {
        if (state.loading) state.loading = true;
      })
      .addCase(loadDashboardDetails.fulfilled, (state, action) => {
        setAll(state, action.payload);
        state.loading = false;
      })
      .addCase(loadDashboardDetails.rejected, (state, { error }) => {
        state.loading = false;
        console.log(error);
      });
  },
});

const baseInfo = (state: RootState) => state.dashboard;

export default appSlice.reducer;

export const { fetchAppSuccess } = appSlice.actions;

export const getAppState = createSelector(baseInfo, dashboard => dashboard);
