import { Hidden } from "@material-ui/core";
import Box from "@material-ui/core/Box";
import "./components.scss";

function InfoCard(props: any) {
  const { dataList, height, headerInfo } = props;

  return (
    <div className="card-container">
      {headerInfo && (
        <Hidden mdUp>
          <div className="header">
            <div className="container">
              <div className="title">{headerInfo.title}</div>
              <Box display="flex" alignItems="baseline" style={{ gap: "6px" }}>
                <div className="value">{headerInfo.value}</div>
                <div className="unit">{headerInfo.unit}</div>
              </Box>
            </div>
          </div>
        </Hidden>
      )}
      {dataList.map((item: any, index: number) =>
        index % 2 === 1 ? (
          <div className="line-container odd-background" style={{ height: height / dataList.length }}>
            <div className="title">{item.name}</div>
            <div className="value">{item.value}</div>
            {item.isButton && (
              <button className="button" onClick={() => item.onClick()}>
                {item.buttonLabel}
              </button>
            )}
          </div>
        ) : (
          <div className="line-container even-background" style={{ height: height / dataList.length }}>
            <div className="title">{item.name}</div>
            <div className="value">{item.value}</div>
            {item.isButton && (
              <button className="button" onClick={() => item.onClick()}>
                {item.buttonLabel}
              </button>
            )}
          </div>
        ),
      )}
    </div>
  );
}

export default InfoCard;
