import { ethers } from "ethers";
import { getAddresses } from "../../constants";
import { TimeTokenContract, MemoTokenContract, BoomerangCapitalABI, wMemoTokenContract } from "../../abi";
import { setAll } from "../../helpers";

import { createSlice, createSelector, createAsyncThunk } from "@reduxjs/toolkit";
import { JsonRpcProvider, StaticJsonRpcProvider } from "@ethersproject/providers";
import { Bond } from "../../helpers/bond/bond";
import { Networks } from "../../constants/blockchain";
import React from "react";
import { RootState } from "../store";
import { IToken } from "../../helpers/tokens";

interface IGetBalances {
  address: string;
  networkID: Networks;
  provider: StaticJsonRpcProvider | JsonRpcProvider;
}

interface IAccountBalances {
  balances: {
    memo: string;
    time: string;
    wmemo: string;
  };
}

export const getBalances = createAsyncThunk("account/getBalances", async ({ address, networkID, provider }: IGetBalances): Promise<IAccountBalances> => {
  const addresses = getAddresses(networkID);

  const memoContract = new ethers.Contract(addresses.BOOMCAPITAL_ADDRESS, MemoTokenContract, provider);
  const memoBalance = await memoContract.balanceOf(address);
  const timeContract = new ethers.Contract(addresses.BOOMCAPITAL_ADDRESS, TimeTokenContract, provider);
  const timeBalance = await timeContract.balanceOf(address);
  const wmemoContract = new ethers.Contract(addresses.BOOMCAPITAL_ADDRESS, wMemoTokenContract, provider);
  const wmemoBalance = await wmemoContract.balanceOf(address);

  return {
    balances: {
      memo: ethers.utils.formatUnits(memoBalance, "gwei"),
      time: ethers.utils.formatUnits(timeBalance, "gwei"),
      wmemo: ethers.utils.formatEther(wmemoBalance),
    },
  };
});

interface ILoadAccountDetails {
  address: string;
  networkID: Networks;
  provider: StaticJsonRpcProvider | JsonRpcProvider;
}

interface IUserAccountDetails {
  balances: {
    time: string;
    memo: string;
    wmemo: string;
  };
  staking: {
    time: number;
    memo: number;
  };
  wrapping: {
    memo: number;
  };
}

export const loadAccountDetails = createAsyncThunk("account/loadAccountDetails", async ({ networkID, provider, address }: ILoadAccountDetails): Promise<IUserAccountDetails> => {
  let timeBalance = 0;
  let memoBalance = 0;

  let wmemoBalance = 0;
  let memoWmemoAllowance = 0;

  let stakeAllowance = 0;
  let unstakeAllowance = 0;

  const addresses = getAddresses(networkID);

  if (addresses.BOOMCAPITAL_ADDRESS) {
    const timeContract = new ethers.Contract(addresses.BOOMCAPITAL_ADDRESS, TimeTokenContract, provider);
    timeBalance = await timeContract.balanceOf(address);
    stakeAllowance = await timeContract.allowance(address, addresses.BOOMCAPITAL_ADDRESS);
  }

  if (addresses.BOOMCAPITAL_ADDRESS) {
    const memoContract = new ethers.Contract(addresses.BOOMCAPITAL_ADDRESS, MemoTokenContract, provider);
    memoBalance = await memoContract.balanceOf(address);
    unstakeAllowance = await memoContract.allowance(address, addresses.BOOMCAPITAL_ADDRESS);

    if (addresses.BOOMCAPITAL_ADDRESS) {
      memoWmemoAllowance = await memoContract.allowance(address, addresses.BOOMCAPITAL_ADDRESS);
    }
  }

  if (addresses.BOOMCAPITAL_ADDRESS) {
    const wmemoContract = new ethers.Contract(addresses.BOOMCAPITAL_ADDRESS, wMemoTokenContract, provider);
    wmemoBalance = await wmemoContract.balanceOf(address);
  }

  return {
    balances: {
      memo: ethers.utils.formatUnits(memoBalance, "gwei"),
      time: ethers.utils.formatUnits(timeBalance, "gwei"),
      wmemo: ethers.utils.formatEther(wmemoBalance),
    },
    staking: {
      time: Number(stakeAllowance),
      memo: Number(unstakeAllowance),
    },
    wrapping: {
      memo: Number(memoWmemoAllowance),
    },
  };
});

interface ICalcUserBondDetails {
  address: string;
  bond: Bond;
  provider: StaticJsonRpcProvider | JsonRpcProvider;
  networkID: Networks;
}

export interface IUserBondDetails {
  allowance: number;
  balance: number;
  avaxBalance: number;
  interestDue: number;
  bondMaturationBlock: number;
  pendingPayout: number; //Payout formatted in gwei.
}

export const calculateUserBondDetails = createAsyncThunk("account/calculateUserBondDetails", async ({ address, bond, networkID, provider }: ICalcUserBondDetails) => {
  if (!address) {
    return new Promise<any>(resevle => {
      resevle({
        bond: "",
        displayName: "",
        bondIconSvg: "",
        isLP: false,
        allowance: 0,
        balance: 0,
        interestDue: 0,
        bondMaturationBlock: 0,
        pendingPayout: "",
        avaxBalance: 0,
      });
    });
  }

  const bondContract = bond.getContractForBond(networkID, provider);
  const reserveContract = bond.getContractForReserve(networkID, provider);

  let interestDue, pendingPayout, bondMaturationBlock;

  const bondDetails = await bondContract.bondInfo(address);
  interestDue = bondDetails.payout / Math.pow(10, 9);
  bondMaturationBlock = Number(bondDetails.vesting) + Number(bondDetails.lastTime);
  pendingPayout = await bondContract.pendingPayoutFor(address);

  let allowance,
    balance = "0";

  allowance = await reserveContract.allowance(address, bond.getAddressForBond(networkID));
  balance = await reserveContract.balanceOf(address);
  const balanceVal = ethers.utils.formatEther(balance);

  const avaxBalance = await provider.getSigner().getBalance();
  const avaxVal = ethers.utils.formatEther(avaxBalance);

  const pendingPayoutVal = ethers.utils.formatUnits(pendingPayout, "gwei");

  return {
    bond: bond.name,
    displayName: bond.displayName,
    bondIconSvg: bond.bondIconSvg,
    isLP: bond.isLP,
    allowance: Number(allowance),
    balance: Number(balanceVal),
    avaxBalance: Number(avaxVal),
    interestDue,
    bondMaturationBlock,
    pendingPayout: Number(pendingPayoutVal),
  };
});

interface ICalcUserTokenDetails {
  address: string;
  token: IToken;
  networkID: Networks;
  provider: StaticJsonRpcProvider | JsonRpcProvider;
}

export interface IUserTokenDetails {
  allowance: number;
  balance: number;
  isEther?: boolean;
}

export const calculateUserTokenDetails = createAsyncThunk("account/calculateUserTokenDetails", async ({ address, token, networkID, provider }: ICalcUserTokenDetails) => {
  if (!address || address === "") {
    return new Promise<any>(resevle => {
      resevle({
        token: "",
        address: "",
        img: "",
        allowance: 0,
        balance: 0,
      });
    });
  }

  if (token.isEther) {
    const ethBalance = await provider.getBalance(address);
    const ethVal = ethers.utils.formatEther(ethBalance);

    return {
      token: token.name,
      tokenIcon: token.img,
      balance: Number(ethVal),
      isEther: true,
    };
  }

  const addresses = getAddresses(networkID);

  const tokenContract = new ethers.Contract(token.address, BoomerangCapitalABI, provider);
  if (token.isFetchAllowance) {
    let allowanceBond,
      allowanceLeverage,
      balance = "0";

    allowanceBond = await tokenContract.allowance(address, addresses.BOND_DEPOSITORY);
    allowanceLeverage = await tokenContract.allowance(address, addresses.LEVERAGE_ADDRESS);
    balance = await tokenContract.balanceOf(address);
    const balanceVal = Number(balance) / Math.pow(10, token.decimals);

    return {
      token: token.name,
      address: token.address,
      img: token.img,
      allowanceBond: Number(allowanceBond),
      allowanceLeverage: Number(allowanceLeverage),
      balance: Number(balanceVal),
    };
  } else {
    let balance = 0;
    balance = await tokenContract.balanceOf(address);
    const balanceVal = Number(balance) / Math.pow(10, token.decimals);
    return {
      token: token.name,
      address: token.address,
      img: token.img,
      balance: Number(balanceVal),
    };
  }
});

export interface IAccountSlice {
  loading: boolean;
  tokens: { [key: string]: IUserTokenDetails };
}

const initialState: IAccountSlice = {
  loading: true,
  tokens: {},
};

const accountSlice = createSlice({
  name: "account",
  initialState,
  reducers: {
    fetchAccountSuccess(state, action) {
      setAll(state, action.payload);
    },
  },
  extraReducers: builder => {
    builder
      .addCase(calculateUserTokenDetails.pending, (state, action) => {
        state.loading = true;
      })
      .addCase(calculateUserTokenDetails.fulfilled, (state, action) => {
        if (!action.payload) return;
        const token = action.payload.token;
        state.tokens[token] = action.payload;
        state.loading = false;
      })
      .addCase(calculateUserTokenDetails.rejected, (state, { error }) => {
        state.loading = false;
        console.log(error);
      });
  },
});

export default accountSlice.reducer;

export const { fetchAccountSuccess } = accountSlice.actions;

const baseInfo = (state: RootState) => state.account;

export const getAccountState = createSelector(baseInfo, account => account);
