import { ethers } from "ethers";
import { getAddresses, Networks } from "../../constants";
import { BondDepositoryABI, BoomSafeLeverageABI, BondDividendTrackerABI, BoomerangCapitalABI } from "../../abi";
import { setAll } from "../../helpers";
import { createSlice, createSelector, createAsyncThunk } from "@reduxjs/toolkit";
import { JsonRpcProvider, StaticJsonRpcProvider } from "@ethersproject/providers";
import { getTokenPrice } from "../../helpers";
import { RootState } from "../store";
import { multicall } from "../../helpers/multicall";
import { error, warning, success, info } from "../slices/messages-slice";
import { messages } from "../../constants/messages";
import { metamaskErrorWrap } from "../../helpers/metamask-error-wrap";
import { fetchPendingTxns, clearPendingTxn } from "./pending-txns-slice";

interface ILoadPortFolioDetails {
  networkID: number;
  provider: JsonRpcProvider;
  userAddress: string;
}

interface IHandleClaim {
  provider: StaticJsonRpcProvider | JsonRpcProvider;
  networkID: Networks;
}

export const handleClaimBoom = createAsyncThunk("bonding/handleClaimBoom", async ({ provider, networkID }: IHandleClaim, { dispatch }) => {
  if (!provider) {
    dispatch(warning({ text: messages.please_connect_wallet }));
    return;
  }
  const addresses = getAddresses(networkID);

  const signer = provider.getSigner();
  const boomContract = new ethers.Contract(addresses.BOOMCAPITAL_ADDRESS, BoomerangCapitalABI, signer);

  let approveTx;
  try {
    approveTx = boomContract.claim();
    dispatch(
      fetchPendingTxns({
        txnHash: approveTx.hash,
        text: "Claim ",
        type: "claim",
      }),
    );
    await approveTx.wait();
    dispatch(success({ text: messages.tx_successfully_send }));
  } catch (err: any) {
    metamaskErrorWrap(err, dispatch);
  } finally {
    if (approveTx) {
      dispatch(clearPendingTxn(approveTx.hash));
    }
  }
});

