export const loadPortFolioDetails = createAsyncThunk(
  "app/loadPortFolioDetails",
  //@ts-ignore
  async ({ networkID, provider, userAddress }: ILoadPortFolioDetails) => {
    const addresses = getAddresses(networkID);
    const dividendTrackerContract = new ethers.Contract(addresses.DIVIDEND_TRACKER_ADDRESS, BondDividendTrackerABI, provider);

    const dividends = await dividendTrackerContract.getAccountInfo(userAddress);
    const bondDepositoryCalls = [
      {
        address: addresses.BOND_DEPOSITORY,
        name: "getBondHolderTokenVesting",
        params: [userAddress],
      },
      {
        address: addresses.BOND_DEPOSITORY,
        name: "getBondHolderAveragePriceOfVestingBonds",
        params: [userAddress],
      },
      {
        address: addresses.BOND_DEPOSITORY,
        name: "getBondHolderVestingTimeRemaining",
        params: [userAddress],
      },
      {
        address: addresses.BOND_DEPOSITORY,
        name: "getBondHolderBondingTime",
        params: [userAddress],
      },
    ];

    const [bondHolderTokenVesting, bondHolderAvgPriceOfVB, bondHolderVestingTimeRemaining, bondHolderBondingTime] = await multicall(
      BondDepositoryABI,
      bondDepositoryCalls,
      provider,
      networkID,
    );
    const leverageCalls = [
      {
        address: addresses.LEVERAGE_ADDRESS,
        name: "getPosition",
        params: [userAddress],
      },
      {
        address: addresses.LEVERAGE_ADDRESS,
        name: "getWETHOwed",
        params: [userAddress],
      },
      {
        address: addresses.LEVERAGE_ADDRESS,
        name: "getLiquidationPrice",
        params: [userAddress],
      },
      {
        address: addresses.LEVERAGE_ADDRESS,
        name: "getCurrentUserLeverageBPS",
        params: [userAddress],
      },
    ];

    const [position, wETHOwed, liquidationPrice, userLeverageBPS] = await multicall(BoomSafeLeverageABI, leverageCalls, provider, networkID);
    return {
      dividends: dividends[0],
      bondInfo: {
        bondHolderTokenVesting: ethers.utils.formatEther(bondHolderTokenVesting[0]),
        bondHolderAvgPriceOfVB: ethers.utils.formatEther(bondHolderAvgPriceOfVB[0]),
        bondHolderVestingTimeRemaining: bondHolderVestingTimeRemaining[0],
        bondHolderBondingTime: bondHolderVestingTimeRemaining[0],
      },
      leverageInfo: {
        position: position[0],
        wETHOwed: ethers.utils.formatEther(wETHOwed[0]),
        liquidationPrice: liquidationPrice[0],
        userLeverageBPS: userLeverageBPS[0],
      },
    };
  },
);

const initialState = {
  loading: true,
};
export interface ILeverageInfo {
  position: any;
  wETHOwed: any;
  liquidationPrice: any;
  userLeverageBPS: any;
}
export interface IBondInfo {
  bondHolderTokenVesting: any;
  bondHolderAvgPriceOfVB: any;
  bondHolderVestingTimeRemaining: any;
  bondHolderBondingTime: any;
}
export interface IPortFolioSlice {
  loading: boolean;
  dividends: any;
  bondInfo: IBondInfo;
  leverageInfo: ILeverageInfo;
}

const appSlice = createSlice({
  name: "portFolio",
  initialState,
  reducers: {
    fetchAppSuccess(state, action) {
      setAll(state, action.payload);
    },
  },
  extraReducers: builder => {
    builder
      .addCase(loadPortFolioDetails.pending, (state, action) => {
        if (state.loading) state.loading = true;
      })
      .addCase(loadPortFolioDetails.fulfilled, (state, action) => {
        setAll(state, action.payload);
        state.loading = false;
      })
      .addCase(loadPortFolioDetails.rejected, (state, { error }) => {
        state.loading = false;
        console.log(error);
      });
  },
});

const baseInfo = (state: RootState) => state.portFolio;

export default appSlice.reducer;

export const { fetchAppSuccess } = appSlice.actions;

export const getAppState = createSelector(baseInfo, portFolio => portFolio);
