import { useEffect, useState, useCallback } from "react";
import { Route, Redirect, Switch } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { useAddress, useWeb3Context } from "../hooks";
import { calcBondDetails } from "../store/slices/bond-slice";
import { loadDashboardDetails } from "../store/slices/dashboard-slice";
import { loadAccountDetails, calculateUserBondDetails, calculateUserTokenDetails } from "../store/slices/account-slice";
import { IReduxState } from "../store/slices/state.interface";
import Loading from "../components/Loader";
import useBonds from "../hooks/bonds";
import useTokens from "../hooks/tokens";
import ViewBase from "../components/ViewBase";
import { Stake, ChooseBond, Bond, Dashboard, Calculator, Landing, Portfolio, TaxAndZones, Treasury } from "../views";
import "./style.scss";

function App() {
  const dispatch = useDispatch();

  const { connect, provider, hasCachedProvider, chainID, connected } = useWeb3Context();
  const address = useAddress();

  const [walletChecked, setWalletChecked] = useState(false);

  const isAppLoaded = useSelector<IReduxState, boolean>(state => (state.dashboard.metricTracker ? !Boolean(state.dashboard.metricTracker.totalHolders > 0) : false));

  const { bonds } = useBonds();
  const { tokens } = useTokens();

  async function loadDetails(whichDetails: string) {
    let loadProvider = provider;
    loadApp(loadProvider);

    if (whichDetails === "userTokens" && address && connected) {
      tokens.map(token => {
        dispatch(calculateUserTokenDetails({ address, token, provider, networkID: chainID }));
      });
    }
  }

  const loadApp = useCallback(
    loadProvider => {
      dispatch(loadDashboardDetails({ networkID: chainID, provider: loadProvider }));
      tokens.map(token => {
        dispatch(calculateUserTokenDetails({ address: "", token, networkID: chainID, provider }));
      });
    },
    [connected],
  );

  // const loadAccount = useCallback(
  //   loadProvider => {
  //     dispatch(loadAccountDetails({ networkID: chainID, address, provider: loadProvider }));
  //   },
  //   [connected],
  // );

  useEffect(() => {
    if (hasCachedProvider()) {
      connect().then(() => {
        setWalletChecked(true);
      });
    } else {
      setWalletChecked(true);
    }
  }, []);

  useEffect(() => {
    if (walletChecked) {
      loadDetails("userTokens");
    }
  }, [walletChecked]);

  return (
    <Switch>
      <Route exact path="/">
        <Landing />
      </Route>
      <ViewBase>
        <Route exact path="/dashboard">
          <Dashboard />
        </Route>
        <Route exact path="/portfolio">
          <Portfolio />
        </Route>
        <Route exact path="/taxandzones">
          <TaxAndZones />
        </Route>
        <Route exact path="/treasury">
          <Treasury />
        </Route>
        <Route exact path="/bond">
          <Bond />
        </Route>
        <Route path="/calculator">
          <Calculator />
        </Route>
      </ViewBase>
    </Switch>
  );
}

export default App;
