export const degree = (num: any) => {
  const match = ("" + num).match(/(?:\.(\d+))?(?:[eE]([+-]?\d+))?$/);
  let decimal = 0;
  if (match === null) return num;
  decimal = Math.max(
    0,
    // Number of digits right of decimal point.
    (match[1] ? match[1].length : 0) - (match[2] ? +match[2] : 0),
  );
  return `0.0(${decimal - 1})${(num * 10 ** (decimal - 1)).toFixed(1).split(".")[1]}`;
};

export const decimalFormator = (num: number) => {
  if (num === 0) {
    return 0;
  } else if (num >= 1000) {
    return num.toFixed(0);
  } else if (num >= 1 && num < 1000) {
    return num.toFixed(2);
  } else if (num >= 0.1 && num < 1) {
    return num.toFixed(3);
  } else if (num >= 0.01 && num < 0.1) {
    return num.toFixed(4);
  } else if (num >= 0.001 && num < 0.01) {
    return num.toFixed(5);
  } else if (num < 0.001) {
    return degree(num);
  }
  return num;
};

export const priceFormater = (num: number, unit: string) => {
  let _number = decimalFormator(num);
  return _number === "Loading..."
    ? "Loading..."
    : unit === ""
    ? _number.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",")
    : _number.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",") + " " + unit;
};

export const getVesting = (seconds: any) => {
  let y = Math.floor(seconds / 31536000);
  let mo = Math.floor((seconds % 31536000) / 2628000);
  let d = Math.floor(((seconds % 31536000) % 2628000) / 86400);
  let h = Math.floor((seconds % (3600 * 24)) / 3600);
  let m = Math.floor((seconds % 3600) / 60);
  let s = Math.floor(seconds % 60);

  let yDisplay = y > 0 ? y + (y === 1 ? " year, " : " years, ") : "";
  let moDisplay = mo > 0 ? mo + (mo === 1 ? " month, " : " months, ") : "";
  let dDisplay = d > 0 ? d + (d === 1 ? " day, " : " days, ") : "";
  let hDisplay = h > 0 ? h + (h === 1 ? " hour, " : " hours, ") : "";
  let mDisplay = m > 0 ? m + (m === 1 ? " minute, " : " minutes, ") : "";
  let sDisplay = s > 0 ? s + (s === 1 ? " second" : " seconds ") : "";
  let result: string = yDisplay + moDisplay + dDisplay + hDisplay + mDisplay + sDisplay;
  result = result.charAt(result.length - 2) === "," ? result.slice(0, -2) : result;
  return result;
};
