export const getMainnetURI = (): string => {
  return "https://api.avax.network/ext/bc/C/rpc";
};

export const getEthereumURI = (): string => {
  return `https://mainnet.infura.io/v3/${process.env.REACT_APP_INFURA_ID}`;
};

export const getRinkeByURI = (): string => {
  return `https://rinkeby.infura.io/v3/${process.env.REACT_APP_INFURA_ID}`;
};
