import React, { useEffect } from "react";
import { Box, Grid, Modal, TextField, Button } from "@material-ui/core";
import "./modal.scss";
import WETHIcon from "../../../assets/tokens/WETH.e.png";
import BoomIcon from "../../../assets/icons/boom.svg";
import ClockIcon from "../../../assets/icons/clock.svg";
import BondTable from "../../Table/BondTable";
import { changeApproval, changeBond } from "../../../store/slices/bond-slice";
import { useDispatch } from "react-redux";
import { useWeb3Context } from "../../../hooks";
import { loadBondDetails } from "../../../store/slices/bond-slice";

const ELEMENT_LIST = [
  {
    name: "Your Balance",
    value: "0%",
  },
  {
    name: "You Will Get",
    value: "0 SPIRIT",
  },
  {
    name: "Max",
    value: "40289.6970",
  },
  {
    name: "ROI",
    value: "-1.61%",
  },
  {
    name: "Vesting Terms",
    value: "7 days",
  },
  {
    name: "Bond Contract",
    value: "View",
  },
];

const style = {
  position: "absolute" as "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 950,
  boxShadow: 24,
  p: 4,
  background: "#0e1f31",
  borderRadius: "4px",
  border: "1px solid rgba(54, 160, 253, 0.48)",
};

interface ModalProps {
  open: any;
  bondData: any;
  handleClose: any;
}
export default function BondModal({ open, bondData, handleClose }: ModalProps) {
  const dispatch = useDispatch();
  const { provider, address, chainID } = useWeb3Context();
  const [tokensToBond, setTokensToBond] = React.useState("");
  const [isApproved, setIsApproved] = React.useState(false);

  useEffect(() => {
    const interval = setInterval(() => {
      dispatch(loadBondDetails({ networkID: chainID, provider, userAddress: address }));
    }, 20000);
    return () => clearInterval(interval);
  }, [address]);

  const onChange = (e: any) => {
    setTokensToBond(`${e.target.value}`);
  };

  console.log("modal allowance", bondData.allowance);
  const onClick = () => {
    if (bondData.allowance > 0) {
      dispatch(changeBond({ bond: bondData, provider, networkID: chainID, tokensToBond }));
      setIsApproved(true);
    } else {
      dispatch(changeApproval({ bond: bondData, provider, networkID: chainID, address }));
      setIsApproved(false);
    }
  };

  return (
    <div>
      <Modal open={open} onClose={handleClose} aria-labelledby="modal-modal-title" aria-describedby="modal-modal-description">
        <Box sx={style}>
          <Grid container spacing={4}>
            <Grid item lg={12} md={12} sm={12} xs={12}>
              <div className="modal-title">ETH Bond ID {bondData.id}</div>
            </Grid>
            <Grid item lg={6} md={6} sm={12} xs={12} className="border-right">
              <div className="price-content">
                <div className="title">Bond Price</div>
                <div className="bond-price">$0.3684</div>
                <div className="crypto-price">0.0000242 WETH</div>
              </div>
            </Grid>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <div className="price-content">
                <div className="title">Market Price</div>
                <div className="market-price">$0.3684</div>
                <div className="crypto-price">0.0000242 WETH</div>
              </div>
            </Grid>
            <Grid item lg={12} md={12} sm={12} xs={12}>
              <div className="border"></div>
            </Grid>

            <Grid item lg={4} md={4}>
              <div className="crypto-item">
                <img src={WETHIcon} />
                <div className="crypto-name">$ETH</div>
              </div>
            </Grid>
            <Grid item lg={4} md={4}>
              <div className="clock-content">
                <img src={ClockIcon} />
                <p>Vested over 7 days</p>
                <div>458,142</div>
              </div>
            </Grid>
            <Grid item lg={4} md={4}>
              <div className="crypto-item">
                <img src={BoomIcon} />
                <div className="crypto-name">$BOOM</div>
              </div>
            </Grid>
            <Grid item lg={12} md={12} sm={12} xs={12}>
              <div className="bond-content">
                <input type="number" onChange={onChange} value={tokensToBond} />
                <Button variant="contained" onClick={onClick}>
                  {bondData.allowance > 0 ? "Bond" : "Aprove"}
                </Button>
              </div>
            </Grid>
            <Grid item lg={12} md={12} sm={12} xs={12}>
              <BondTable dataList={ELEMENT_LIST} width="730" height="138" cellHeight="69px" />
            </Grid>
          </Grid>
        </Box>
      </Modal>
    </div>
  );
}
