import { configureStore } from "@reduxjs/toolkit";

import accountReducer from "./slices/account-slice";
import bondingReducer from "./slices/bond-slice";
import dashboardReducer from "./slices/dashboard-slice";
import leverageReducer from "./slices/leverage-slice";
import messagesReducer from "./slices/messages-slice";
import pendingTransactionsReducer from "./slices/pending-txns-slice";
import portFolioReducer from "./slices/portfolio-slice";
import taxZoneReducer from "./slices/tax-zone-slice";
import treasuryReducer from "./slices/treasury-slice";

const store = configureStore({
  reducer: {
    account: accountReducer,
    bonding: bondingReducer,
    dashboard: dashboardReducer,
    leverage: leverageReducer,
    messages: messagesReducer,
    pendingTransactions: pendingTransactionsReducer,
    portFolio: portFolioReducer,
    taxandzone: taxZoneReducer,
    treasury: treasuryReducer,
  },
  middleware: getDefaultMiddleware => getDefaultMiddleware({ serializableCheck: false }),
});

export type RootState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;
export default store;
