export interface IBond {
  id: number;
  allowance: any;
  assetBond: string;
  tokenPrice: any;
  totalAmount: any;
  amountLeft: any;
  vestingTerm: any;
  roi: any;
}
export interface IBondSlice {
  loading: boolean;
  bonds: IBond[] | null;
  [key: string]: any;
}

const initialState: IBondSlice = {
  loading: true,
  bonds: null,
};

const setBondState = (state: IBondSlice, payload: any) => {
  const bond = payload.bond;
  const newState = { ...state[bond], ...payload };
  state[bond] = newState;
  state.loading = false;
};

const bondingSlice = createSlice({
  name: "bonding",
  initialState,
  reducers: {
    fetchBondSuccess(state, action) {
      state[action.payload.bond] = action.payload;
    },
  },
  extraReducers: builder => {
    builder
      .addCase(loadBondDetails.pending, (state, action) => {
        if (state.loading) state.loading = true;
      })
      .addCase(loadBondDetails.fulfilled, (state, action) => {
        setAll(state, action.payload);
        state.loading = false;
      })
      .addCase(loadBondDetails.rejected, (state, { error }) => {
        state.loading = false;
        console.log(error);
      });
  },
});

export default bondingSlice.reducer;

export const { fetchBondSuccess } = bondingSlice.actions;

const baseInfo = (state: RootState) => state.bonding;

export const getBondingState = createSelector(baseInfo, bonding => bonding);
