import { AppBar, Toolbar, Link } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import MenuIcon from "../../assets/icons/hamburger.svg";

import ConnectButton from "./connect-button";
import { shorten } from "../../helpers";
import { useAddress } from "../../hooks";
import "./header.scss";
import { DRAWER_WIDTH, TRANSITION_DURATION } from "../../constants/style";

interface IHeader {
  handleDrawerToggle: () => void;
  drawe: boolean;
}

const useStyles = makeStyles(theme => ({
  appBar: {
    [theme.breakpoints.up("sm")]: {
      width: "100%",
      padding: "14px 0",
    },
    justifyContent: "flex-end",
    alignItems: "flex-end",
    background: "transparent",
    backdropFilter: "none",
    zIndex: 10,
  },
  topBar: {
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.sharp,
      duration: TRANSITION_DURATION,
    }),
    zIndex: 1,
    marginLeft: DRAWER_WIDTH,
    background: "transparent",
    borderBottom: "1px solid #66686A",
  },
  topBarShift: {
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.easeOut,
      duration: TRANSITION_DURATION,
    }),
    marginLeft: 0,
  },
}));

function Header({ handleDrawerToggle, drawe }: IHeader) {
  const classes = useStyles();
  const address = useAddress();
  const isVerySmallScreen = useMediaQuery("(max-width: 400px)");
  const isWrapShow = useMediaQuery("(max-width: 480px)");

  return (
    <div className={`${classes.topBar} ${!drawe && classes.topBarShift}`}>
      <AppBar position="sticky" className={classes.appBar} elevation={0}>
        <Toolbar disableGutters className="dapp-topbar">
          <div onClick={handleDrawerToggle} className="dapp-topbar-slider-btn">
            <img src={MenuIcon} alt="" />
          </div>
          <div className="dapp-topbar-btns-wrap">
            {address && (
              <div className="wallet-link">
                <svg width="14" height="16" viewBox="0 0 14 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <circle cx="7" cy="3.09375" r="3" fill="#29C5F6" />
                  <path
                    d="M0 13.9062C0 10.5925 2.68629 7.90625 6 7.90625H8C11.3137 7.90625 14 10.5925 14 13.9062C14 15.0108 13.1046 15.9062 12 15.9062H2C0.89543 15.9062 0 15.0108 0 13.9062Z"
                    fill="#29C5F6"
                  />
                </svg>

                <Link href={`https://rinkeby.etherscan.io/address/${address}`} target="_blank">
                  <p>{shorten(address)}</p>
                </Link>
              </div>
            )}
            <ConnectButton />
          </div>
        </Toolbar>
      </AppBar>
    </div>
  );
}

export default Header;
