import { ethers } from "ethers";
import { DEFAULD_NETWORK, getAddresses, Networks } from "../../constants";
import { BondDepositoryABI, BoomTaxManagerABI, BoomPriceFetcherABI, BoomerangCapitalABI, BoomTreasuryABI, BoomMetricsTrackerABI } from "../../abi";
import { setAll } from "../../helpers";
import { createSlice, createSelector, createAsyncThunk } from "@reduxjs/toolkit";
import { JsonRpcProvider } from "@ethersproject/providers";
import { getMarketPrice, getTokenPrice } from "../../helpers";
import { RootState } from "../store";
import { multicall } from "../../helpers/multicall";

interface ILoadTreasuryDetails {
  networkID: number;
  provider: JsonRpcProvider;
  userAddress: string;
}

export const loadTreasuryDetails = createAsyncThunk(
  "app/loadTreasuryDetails",
  //@ts-ignore
  async ({ networkID, provider, userAddress }: IloadTreasuryDetails) => {
    const addresses = getAddresses(networkID);
    const signer = provider.getSigner();
    const boomTreasuryContract = new ethers.Contract(addresses.TREASURY_ADDRESS, BoomTreasuryABI, signer);

    const totalIndexofAssets = await boomTreasuryContract.getLastIndexNumber();
    // const totalTreasuryValue = await boomTreasuryContract.getUpdatedTreasuryValue();

    // console.log("totalTreasuryValue=", totalTreasuryValue);

    let treasuryArrays: any[] = [];
    for (let i = 0; i < totalIndexofAssets; i++) {
      const treasuryInfo = await boomTreasuryContract.getTreasuryAssetIntVarsAsArrayById(i);
      treasuryArrays =
        treasuryArrays.length === 0
          ? [
              {
                chainid: parseInt(treasuryInfo[0]),
                quantity: parseInt(treasuryInfo[1]),
                priceinweth: ethers.utils.formatEther(treasuryInfo[2]),
                priceinusdc: ethers.utils.formatEther(treasuryInfo[3]),
                valueinweth: ethers.utils.formatEther(treasuryInfo[4]),
                valueinusdc: ethers.utils.formatEther(treasuryInfo[5]),
              },
            ]
          : [
              ...treasuryArrays,
              {
                chainid: parseInt(treasuryInfo[0]),
                quantity: parseInt(treasuryInfo[1]),
                priceinweth: ethers.utils.formatEther(treasuryInfo[2]),
                priceinusdc: ethers.utils.formatEther(treasuryInfo[3]),
                valueinweth: ethers.utils.formatEther(treasuryInfo[4]),
                valueinusdc: ethers.utils.formatEther(treasuryInfo[5]),
              },
            ];
    }
    return {
      treasurys: treasuryArrays,
      totalnumberofassets: totalIndexofAssets,
    };
  },
);

export interface ITreasury {
  chainid: number;
  quantity: number;
  priceinweth: number;
  priceinusdc: number;
  valueinweth: number;
  valueinusdc: number;
}

export interface ITreasurySlice {
  loading: boolean;
  treasurys: ITreasury[] | null;
  [key: string]: any;
}

const initialState: ITreasurySlice = {
  loading: true,
  treasurys: null,
};

const setTreasuryState = (state: ITreasurySlice, payload: any) => {
  const treasury = payload.treasury;
  const newState = { ...state[treasury], ...payload };
  state[treasury] = newState;
  state.loading = false;
};

const treasurySlice = createSlice({
  name: "treasury",
  initialState,
  reducers: {
    fetchTaxAndZoneSuccess(state, action) {
      state[action.payload.treasury] = action.payload;
    },
  },
  extraReducers: builder => {
    builder
      .addCase(loadTreasuryDetails.pending, (state, action) => {
        if (state.loading) state.loading = true;
      })
      .addCase(loadTreasuryDetails.fulfilled, (state, action) => {
        setAll(state, action.payload);
        state.loading = false;
      })
      .addCase(loadTreasuryDetails.rejected, (state, { error }) => {
        state.loading = false;
        console.log(error);
      });
  },
});

const appSlice = createSlice({
  name: "app",
  initialState,
  reducers: {
    fetchAppSuccess(state, action) {
      setAll(state, action.payload);
    },
  },
  extraReducers: builder => {
    builder
      .addCase(loadTreasuryDetails.pending, (state, action) => {
        state.loading = true;
      })
      .addCase(loadTreasuryDetails.fulfilled, (state, action) => {
        setAll(state, action.payload);
        state.loading = false;
      })
      .addCase(loadTreasuryDetails.rejected, (state, { error }) => {
        state.loading = false;
        console.log(error);
      });
  },
});

const baseInfo = (state: RootState) => state.treasury;

export default appSlice.reducer;

export const { fetchAppSuccess } = appSlice.actions;

export const getAppState = createSelector(baseInfo, treasury => treasury);
