import { ethers } from "ethers";
import { DEFAULD_NETWORK, getAddresses, Networks } from "../../constants";
import { BondDepositoryABI, BoomTaxManagerABI, BoomPriceFetcherABI, BoomerangCapitalABI, BoomTreasuryABI, BoomMetricsTrackerABI } from "../../abi";
import { setAll } from "../../helpers";
import { createSlice, createSelector, createAsyncThunk } from "@reduxjs/toolkit";
import { JsonRpcProvider } from "@ethersproject/providers";
import { getMarketPrice, getTokenPrice } from "../../helpers";
import { RootState } from "../store";
import { multicall } from "../../helpers/multicall";

interface ILoadTaxAndZoneDetails {
  networkID: number;
  provider: JsonRpcProvider;
  userAddress: string;
}

export const loadTaxAndZoneDetails = createAsyncThunk(
  "app/loadTaxAndZoneDetails",
  //@ts-ignore
  async ({ networkID, provider, userAddress }: ILoadTaxAndZoneDetails) => {
    const addresses = getAddresses(networkID);
    const boomTaxManagerContract = new ethers.Contract(addresses.TAXMANAGER_ADDRESS, BoomTaxManagerABI, provider);

    const boomTaxManagerCalls = [
      {
        address: addresses.TAXMANAGER_ADDRESS,
        name: "getCurrentTaxRegimeArray",
        params: [],
      },
      {
        address: addresses.TAXMANAGER_ADDRESS,
        name: "getBuyZone2TaxRegimeArray",
        params: [],
      },
      {
        address: addresses.TAXMANAGER_ADDRESS,
        name: "getBuyZone1TaxRegimeArray",
        params: [],
      },
      {
        address: addresses.TAXMANAGER_ADDRESS,
        name: "getFairZoneTaxRegimeArray",
        params: [],
      },
      {
        address: addresses.TAXMANAGER_ADDRESS,
        name: "getSellZone1TaxRegimeArray",
        params: [],
      },
      {
        address: addresses.TAXMANAGER_ADDRESS,
        name: "getSellZone2TaxRegimeArray",
        params: [],
      },
    ];

    const [currentTaxRegimeArray, buyZone2TaxRegimeArray, buyZone1TaxRegimeArray, fairZoneTaxRegimeArray, sellZone1TaxRegimeArray, sellZone2TaxRegimeArray] = await multicall(
      BoomTaxManagerABI,
      boomTaxManagerCalls,
      provider,
      networkID,
    );

    let taxandzoneArrays: any[] = [];
    let currentStatus =
      currentTaxRegimeArray === buyZone2TaxRegimeArray
        ? "Buy Zone 2"
        : currentTaxRegimeArray === buyZone1TaxRegimeArray
        ? "Buy Zone 1"
        : currentTaxRegimeArray === fairZoneTaxRegimeArray
        ? "Fair Zone"
        : currentTaxRegimeArray === sellZone1TaxRegimeArray
        ? "Sell Zone 1"
        : "Sell Zone 2";
    for (let i = 0; i < currentTaxRegimeArray.length; i++) {
      taxandzoneArrays =
        taxandzoneArrays.length === 0
          ? [
              {
                currentzone: ethers.utils.formatEther(currentTaxRegimeArray[i]),
                buyzone2: ethers.utils.formatEther(buyZone2TaxRegimeArray[i]),
                buyzone1: ethers.utils.formatEther(buyZone1TaxRegimeArray[i]),
                fairzone: ethers.utils.formatEther(fairZoneTaxRegimeArray[i]),
                sellzone1: ethers.utils.formatEther(sellZone1TaxRegimeArray[i]),
                sellzone2: ethers.utils.formatEther(sellZone2TaxRegimeArray[i]),
              },
            ]
          : [
              ...taxandzoneArrays,
              {
                currentzone: ethers.utils.formatEther(currentTaxRegimeArray[i]),
                buyzone2: ethers.utils.formatEther(buyZone2TaxRegimeArray[i]),
                buyzone1: ethers.utils.formatEther(buyZone1TaxRegimeArray[i]),
                fairzone: ethers.utils.formatEther(fairZoneTaxRegimeArray[i]),
                sellzone1: ethers.utils.formatEther(sellZone1TaxRegimeArray[i]),
                sellzone2: ethers.utils.formatEther(sellZone2TaxRegimeArray[i]),
              },
            ];
    }
    return {
      taxandzones: taxandzoneArrays,
      currentStatus: currentStatus,
    };
  },
);

export interface ITaxAndZone {
  currentzone: any;
  buyzone2: any;
  buyzone1: any;
  fairzone: any;
  sellzone1: any;
  sellzone2: any;
}
export interface ITaxAndZoneSlice {
  loading: boolean;
  taxandzones: ITaxAndZone[] | null;
  currentStatus: any | null;
  [key: string]: any;
}

const initialState: ITaxAndZoneSlice = {
  loading: true,
  taxandzones: null,
  currentStatus: null,
};

const setTaxAndZoneState = (state: ITaxAndZoneSlice, payload: any) => {
  const taxandzone = payload.taxandzone;
  const newState = { ...state[taxandzone], ...payload };
  state[taxandzone] = newState;
  state.loading = false;
};

const taxandzoneSlice = createSlice({
  name: "taxandzone",
  initialState,
  reducers: {
    fetchTaxAndZoneSuccess(state, action) {
      state[action.payload.taxandzone] = action.payload;
    },
  },
  extraReducers: builder => {
    builder
      .addCase(loadTaxAndZoneDetails.pending, (state, action) => {
        if (state.loading) state.loading = true;
      })
      .addCase(loadTaxAndZoneDetails.fulfilled, (state, action) => {
        setAll(state, action.payload);
        state.loading = false;
      })
      .addCase(loadTaxAndZoneDetails.rejected, (state, { error }) => {
        state.loading = false;
        console.log(error);
      });
  },
});

export default taxandzoneSlice.reducer;

export const { fetchTaxAndZoneSuccess } = taxandzoneSlice.actions;

const baseInfo = (state: RootState) => state.taxandzone;

export const getTaxAndZoneState = createSelector(baseInfo, taxandzone => taxandzone);
