import { useCallback, useState } from "react";
import { NavLink, useLocation } from "react-router-dom";
import Social from "./social";
import DocIcon from "../../../assets/icons/stake.svg";
import WonderlandIcon from "../../../assets/icons/logo_text.svg";
import DashboardIcon from "../../../assets/icons/dashboard.svg";
import DashboardActiveIcon from "../../../assets/icons/dashboard_active.svg";
import PortfolioIcon from "../../../assets/icons/portfolio.svg";
import PortfolioActiveIcon from "../../../assets/icons/portfolio_active.svg";
import TaxJonesIcon from "../../../assets/icons/tax-zones.svg";
import TaxJonesActiveIcon from "../../../assets/icons/tax-zones_active.svg";
import TreasuryIcon from "../../../assets/icons/treasury.svg";
import TreasuryActiveIcon from "../../../assets/icons/treasury_active.svg";
import BondIcon from "../../../assets/icons/bond.svg";
import BondActiveIcon from "../../../assets/icons/bond_active.svg";

import { trim, shorten } from "../../../helpers";
import useBonds from "../../../hooks/bonds";
import { Link } from "@material-ui/core";
import { Skeleton } from "@material-ui/lab";
import "./drawer-content.scss";
import DocsIcon from "../../../assets/icons/stake.svg";
import GlobeIcon from "../../../assets/icons/wonderglobe.svg";
import classnames from "classnames";

function NavContent() {
  const [isActive] = useState();
  const { bonds } = useBonds();
  const location = useLocation();

  const checkPage = useCallback((location: any, page: string): boolean => {
    const currentPath = location.pathname.replace("/", "");
    if (currentPath.indexOf("dashboard") >= 0 && page === "dashboard") {
      return true;
    }
    if (currentPath.indexOf("portfolio") >= 0 && page === "portfolio") {
      return true;
    }
    if (currentPath.indexOf("taxandzones") >= 0 && page === "taxandzones") {
      return true;
    }
    if (currentPath.indexOf("bond") >= 0 && page === "bond") {
      return true;
    }
    if (currentPath.indexOf("treasury") >= 0 && page === "treasury") {
      return true;
    }
    return false;
  }, []);

  return (
    <div className="dapp-sidebar">
      <div className="branding-header">
        <Link href="/">
          <img alt="a" src={WonderlandIcon} width="280px" height="40px" />
        </Link>
      </div>

      <div className="dapp-menu-links">
        <div className="dapp-nav">
          <Link
            component={NavLink}
            to="/dashboard"
            isActive={(match: any, location: any) => {
              return checkPage(location, "dashboard");
            }}
            className={classnames("button-dapp-menu", { active: isActive })}
          >
            <div className="dapp-menu-item">
              <img alt="" src={DashboardIcon} />
              <p>Dashboard</p>
            </div>
          </Link>
          <Link
            component={NavLink}
            to="/portfolio"
            isActive={(match: any, location: any) => {
              return checkPage(location, "portfolio");
            }}
            className={classnames("button-dapp-menu", { active: isActive })}
          >
            <div className="dapp-menu-item">
              <img alt="" src={PortfolioIcon} />
              <p>Portfolio</p>
            </div>
          </Link>
          <Link
            component={NavLink}
            to="/taxandzones"
            isActive={(match: any, location: any) => {
              return checkPage(location, "taxandzones");
            }}
            className={classnames("button-dapp-menu", { active: isActive })}
          >
            <div className="dapp-menu-item">
              <img alt="" src={TaxJonesIcon} />
              <p>Taxs and Zones</p>
            </div>
          </Link>
          <Link
            component={NavLink}
            to="/treasury"
            isActive={(match: any, location: any) => {
              return checkPage(location, "treasury");
            }}
            className={classnames("button-dapp-menu", { active: isActive })}
          >
            <div className="dapp-menu-item">
              <img alt="" src={TreasuryIcon} />
              <p>Treasury</p>
            </div>
          </Link>
          <Link
            component={NavLink}
            to="/bond"
            isActive={(match: any, location: any) => {
              return checkPage(location, "bond");
            }}
            className={classnames("button-dapp-menu", { active: isActive })}
          >
            <div className="dapp-menu-item">
              <img alt="" src={BondIcon} />
              <p>Bonding</p>
            </div>
          </Link>

          <Link
            component={NavLink}
            to="/docs"
            isActive={(match: any, location: any) => {
              return checkPage(location, "docs");
            }}
            className={classnames("button-dapp-menu", { active: isActive })}
          >
            <div className="dapp-menu-item">
              <img alt="" src={DocIcon} />
              <p>Docs</p>
            </div>
          </Link>

          <Link
            component={NavLink}
            id="bond-nav"
            to="/addresses"
            isActive={(match: any, location: any) => {
              return checkPage(location, "addressses");
            }}
            className={classnames("button-dapp-menu", { active: isActive })}
          >
            <div className="dapp-menu-item">
              <img alt="" src={DocIcon} />
              <p>Addresses</p>
            </div>
          </Link>

          {/* <div className="bond-discounts">
            <p>Mint discounts</p>
            {bonds.map((bond, i) => (
              <Link component={NavLink} to={`/mints/${bond.name}`} key={i} className={"bond"}>
                {!bond.bondDiscount ? (
                  <Skeleton variant="text" width={"150px"} />
                ) : (
                  <p>
                    {bond.displayName}
                    <span className="bond-pair-roi">{bond.bondDiscount && trim(bond.bondDiscount * 100, 2)}%</span>
                  </p>
                )}
              </Link>
            ))}
          </div> */}

          {/* <Link
            component={NavLink}
            to="/calculator"
            isActive={(match: any, location: any) => {
              return checkPage(location, "calculator");
            }}
            className={classnames("button-dapp-menu", { active: isActive })}
          >
            <div className="dapp-menu-item">
              <img alt="" src={GlobeIcon} />
              <p>Calculator</p>
            </div>
          </Link> */}
        </div>
      </div>
      <div className="dapp-menu-doc-link" />
      <Social />
    </div>
  );
}

export default NavContent;
